import {mapState, mapGetters} from 'vuex';
import {uniCard, uniIcons} from '@dcloudio/uni-ui';
import collapse from '../../components/collapse';
export default {
  props: {},
  components: {
    uniCard,
    uniIcons,
    collapse,
  },
  computed: {
    ...mapState('detail', {}),
    ...mapGetters('detail', ['doc']),
  },
  asyncComputed: {
    bugs: {
      async get() {
        if (!this.doc) {
          return [];
        }
        let bugIds = [];
        for (let item of this.doc.archive.report.bugs) {
          bugIds.push(item.bugID);
        }
        if (bugIds.length == 0) {
          return [];
        }
        let res = await uni.$ax.request({
          url: `/bug?where=${JSON.stringify(bugIds)}`,
          method: 'GET',
        });
        let bugs = [];
        for (let item of this.doc.archive.report.bugs) {
          let {bugID, name} = item;
          let bug = res.data.find(n => {
            return n.id == bugID;
          });
          let data = {...item, id: bug.id, summary: bug.summary};
          bugs.push(data);
        }
        return bugs;
      },
    },
  },
  watch: {},
  filters: {},
  data() {
    return {};
  },
  methods: {
    navigato(url) {
      uni.navigateTo({
        url: `/pages/alink/index?url=${url}`,
        success: res => {},
        fail: () => {},
        complete: () => {},
      });
    },
  },
  async onLoad(e) {
    console.log(this.$store);
  },
};
